import React from 'react';
import Link from 'gatsby-link';
import Punchline from '../components/punchline';
import EinsteinLogo from '../images/einstein-icon.png';

import { Card, CardTitle, CardText, Col, Row, Tooltip } from 'reactstrap';
//rightAngle L73 <i className="fa fa-circle-o-notch fa-spin fa-2x fa-fw"/>
//<a href="#" className="fa-ul">  <i className=" fa-li fa fa-angle-right"/>
