const portfolioDataRecent = [
  { logo: require('../images/einstein-icon.png'), title: 'European Insurance', blogid: 'strategy', bloglink:'/blog#strategy', blogref:'Strategic Upgrade: Planning On Premise to PaaS Cloud Migration', details:'Integration Architect for Service Cloud implementation connected with Informatica MDM, Mule ESB, Community to deliver the excellent customer service through multiple channels like Avaya Telephony, SMS, Web chat and Social channels. Customer can register and search for Quotes, Policy, Knowledge with digital platform integrated with community' },
  { logo: require('../images/custom-development-icon.png'), title: 'Multi-National Aviation', blogid: 'microservices', bloglink:'/blog#microservices', blogref:'Design Microservices: Using Apache & Free/ Open Source License Model', details:'Sales Automation and platform integration with deal modeling apps and databases behind firewall through Informatica cloud. Multi-lingual LEX (lightning components based) community portal implementation; Role is inclusive of Business process design, environment strategy planning, business demo/ discussions, partner data migration & releases' },
  { logo: require('../images/bots-icon.png'), title: 'AnZ Healthcare Co', blogid: 'portability', bloglink:'/blog#portability', blogref:'Solution Portability: Solution Package Portability across SaaS Vendor', details:'Technical Architect for Mobility solution integration with Inverse data sharing model and Offline data synchronization for iPad based apps. Major business divisions were managing their sales process with different org and coordination was challenging. Design and build solution to consolidate different Business units into a single ORG for better business RoI' },
];

const portfolioDataEarly = [
  { logo: require('../images/iot-icon.png'), title: 'Force.com Consulting', blogid: 'integration', bloglink:'/blog#integration', blogref:'App Security: Social Authentication & Data Integration', details:'Solution design & Force.com based Web, Rest, Bulk, Metadata, oAuth, SSO API services integration for sales automation. Modified open source Salesforce Dataloader codebase to handle excel based data/ metadata sync-up based on configuration work book for Force.com eco-system. Ant metadata migration for deployment, ETL process automation' },
  { logo: require('../images/mobile-icon.png'), title: 'Prepaid Telecom Billing', blogid: 'process', bloglink:'/blog#process', blogref:'Business Process: Manage Workflow & Approval', details:'Prepaid billing systems framework with Mule ESB, Business rules & processes integration. Rule engine allows systems to encapsulate excel based business logic -request validation, subscriber state, promotion status and maintain separate from workflow execution. Build automation & continuous integration with Maven, Sonar, Jenkins & Webdriver' },
  { logo: require('../images/einstein-icon.png'), title: 'Ratetiger: Pricing Tool', blogid: 'polyglot', bloglink:'/blog#polyglot', blogref:'Polyglot SDLC: Elegant UI, Release Portfolio Site', details:'Development of Real time web data mining for hospitality product that empower hoteliers to compare rates with competitors on a real-time basis, and update inventories based on strategies over wide variety of distribution channels. Lucene search prototypes, Solution deployment, Installer package, Compiler tuning & Load balancing for linux containers JVMs' },
];

class portfolioDisplay extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      tooltipOpen: false
    };
  }

  toggle() {
    this.setState({
      tooltipOpen: !this.state.tooltipOpen
    });
  }

  render(){

  	return (
  	<div className="animated fadeIn">
  		<Punchline tag="*Portfolio Synopsis, Micro-Blogging Platform & Hightech Apps to demonstrate Force.com integration with open source enterprise friendly library for higher RoI*" underline={true}/>
  		<Row>
	      	{
	       	portfolioDataRecent.map(item =>
	        <Col xs="12" sm="6" lg="4" key={item.title}>
	            <Card body>
	            	<CardTitle>
		          		<div className="container-fluid" style={{color: '#01344e'}}><img src={EinsteinLogo} width="40" height="40"/>&nbsp;{item.title}</div>
		          	</CardTitle>
		          	<CardText>{item.details}</CardText>
		          	<Link to={item.bloglink} id={item.blogid}>
		          		<i className="fa fa-shield fa-rotate-270"/>&nbsp;{item.blogref}
		          		<Tooltip placement="auto" isOpen={this.state.tooltipOpen} target={item.blogid} toggle={this.toggle}>
         					{item.blogref}
        				</Tooltip>
			    	</Link>
	        	</Card>
	        </Col>
	        )
	      	}
	    </Row>
	    <Row>
	      	{
	       	portfolioDataEarly.map(item =>
	        <Col xs="12" sm="6" lg="4" key={item.title}>
	            <Card body>
	            	<CardTitle>
		          		<div className="container-fluid" style={{color: '#01344e'}}><img src={EinsteinLogo} width="40" height="40"/>&nbsp;{item.title}</div>
		          	</CardTitle>
		          	<CardText>{item.details}</CardText>
		          	<Link to={item.bloglink} id={item.blogid}>
		          		<i className="fa fa-shield fa-rotate-270"/>&nbsp;{item.blogref}
		          		<Tooltip placement="auto" isOpen={this.state.tooltipOpen} target={item.blogid} toggle={this.toggle}>
         					{item.blogref}
        				</Tooltip>
			    	</Link>
	        	</Card>
	        </Col>
	        )
	      	}
	    </Row>
    </div>
  	);
  }
}
